
import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import AddTagName from '@/components/AddTag/AddTagName.vue';
import TagList from '@/components/AddTag/TagList.vue';
import router from '@/router';

@Component({
  components: {AddTagName, TagList},
})
export default class addTags extends Vue {
  //定义变量
  name: string = '';
  icon: string = '';

  get tagList() {
    return this.$store.state.tagList;
  }

  created() {
    this.$store.commit('fetchTags');
  }

  // 接收新增分类名称
  getTagName(name: string) {
    this.name = name;
  }

  getTagIcon(icon: string) {
    this.icon = icon;
  }

  map: { [key: string]: string } = {
    'tag name duplicated': '标签已存在',
    'create tag success': '添加成功'
  };

  // 添加标签
  createTag() {
    const name = this.name;
    const icon = this.icon;
    if (!name) {
      window.alert('请输入标签名');
      return;
    }
    if (!icon) {
      window.alert('请选择标签');
      return;
    }
    if (name && icon) {
      this.$store.commit('createTag', {name, icon});
      if (this.$store.state.createTagError) {
        if (this.$store.state.createTagError.message === 'tag name duplicated') {
          window.alert('标签已存在');
          return;
        } else if (this.$store.state.createTagError.message === 'create tag success') {
          window.alert('添加成功');
          router.replace('/money');
          return;
        } else {
          window.alert('未知错误');
          return;
        }
      }
    }
  }

  back() {
    router.back();
  }
}
